import { ChangeEvent, CSSProperties, FocusEvent, KeyboardEvent, useRef, useState } from "react";
import './button.scss';

export type InputButtonPropType = {
    icon?: JSX.Element | (() => JSX.Element);
    onChange?: (e?: ChangeEvent<HTMLInputElement>) => void;
    onEnter?: (val: string) => void;
    value?: string;
    text?: string;
    color?: string;
    background?: string;
    style?: CSSProperties
};
export const InputButton = ({ icon, onChange, onEnter, value, text, color, style, background }: InputButtonPropType) => {
    const [clicked, setClicked] = useState(false);
    const dataVal = useRef('');
    const handleEnter = (e: KeyboardEvent<HTMLInputElement>) => {
        if (e?.key && e?.key?.toLowerCase() === 'enter') {
            if (onEnter) {
                onEnter((e?.target as HTMLInputElement).value);
            }
            setClicked(false);
            dataVal.current = '';
        } else if (e?.key && e?.key?.toLowerCase() === 'tab') {
            if (onEnter) {
                onEnter((e?.target as HTMLInputElement).value);
            }
            setClicked(false);
            dataVal.current = '';
        }
    }
    const handleBlur = (e: FocusEvent) => {
        dataVal.current = (e?.target as HTMLInputElement).value;
        setClicked(false);
    }
    return (
        <div style={{ ...(style ?? {}), color, background }} className="input-button-container">
            {
                clicked ? (
                    <input
                        className="input-button-container__input"
                        placeholder={text}
                        autoFocus
                        onBlur={handleBlur}
                        onKeyDown={handleEnter}
                        {...( value ? { value } : {})}
                        {...( onChange ? { onChange } : {})}
                        defaultValue={dataVal.current}
                    />
                ) : (
                    <>
                        <button className="input-button-container__btn" onFocus={() => setClicked(true)}>
                            {
                                typeof icon === 'function' ? icon() : icon
                            }
                            { text }
                        </button>
                    </>
                )
            }
        </div>
    )
};
